<template>
    <div class="headerVue">
        <img :src="image" class="img-fluid" alt="">

        <div class="flex-content">
            <div class="flex-container">
                <div class="row">
                <div class="flex-item">
                    <center>
                        <div class="flex-header">
                    
                        </div>
                    </center>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            image:require('@/assets/aboutus/headerBackground.png')
        }
    },
    mounted() {
        const vm = this;
        vm.image = require('@/assets/citys/countrys/' + vm.$route.params.country + '.png');
    }
}
</script>
<style>
.headerVue{position: relative;}
.flex-content{position: absolute; left: 0px; top: 0px; width: 100%; margin-top: 2%;}
.flex-container {padding: 0;margin: 0;list-style: none;display: -webkit-box;display: -moz-box;display: -ms-flexbox;display: -webkit-flex;display: flex;align-items: center;justify-content: center;align-content: center;}
.flex-row{width: 100%;}
.flex-item {align-self: center;color: white;font-weight: bold;text-align: center;-webkit-text-stroke: 0.5px black;}
.flex-header{text-align: center;font-size:32px;font-weight: 500;padding:20px 10px;}
.flex-contentArea{margin-top: 25px;text-align: center; font-size: xx-large;}
</style>


