<template>
	<div class="screenShot">
		<div class="headerTitle" data-aos="zoom-in">
			Rider guidance
		</div>
		<div class="afterHeader" data-aos="zoom-in">
		</div>
		<carousel-3d class="screenShotCarousel" style="height: 600px; background-color: transparent; border-width:0px; ">
		    <slide v-for="i in rider" :index="i.index" :key="i.index" style="height: 600px;">
		        <template >
		            <img :data-index="i.index" :src="i.img" style="height: 550px; width:307px;" data-aos="zoom-in">
		            <!-- <div class="te" style="text-align: center;">{{i.element}}</div> -->
		            <p class="tx">{{i.element}} - {{i.step}}</p>
		        </template>
		    </slide>
		</carousel-3d>
	</div>
</template>
<script>
export default {
	data() {
		return {
			rider:[
				{img:require('@/assets/steps/rider/1.png'),index:0,element:'Step 1',step:'Where to go ?'},
				{img:require('@/assets/steps/rider/2.png'),index:1,element:'Step 2',step:'Choose a destination'},
				{img:require('@/assets/steps/rider/3.png'),index:2,element:'Step 3',step:'Confirm trip'},
				{img:require('@/assets/steps/rider/4.png'),index:3,element:'Step 4',step:'Wait for a ride'},
				{img:require('@/assets/steps/rider/5.png'),index:4,element:'Step 5',step:'Rate trip'},
			]
		}
	}
	}
</script>
<style>
	.screenShot{padding: 90px 0px;width:100%; text-align: center;}
	.screenShot .headerTitle { font-size: 32px; color:black; font-weight: 600; text-align: center; }
	.screenShot .afterHeader{font-size: 18px; color:black;  padding: 5px 5px;}
	.Images{margin-top: 40px;}
	.screenShotCarousel{height: 600px!important;}
	.screenShotCarousel .carousel-3d-slide{border-width: 0px!important; border-color: transparent; background-color:transparent!important; height:600px!important;}
	.screenShotCarousel .carousel-3d-slider{height:600px!important;}
	.te{position: absolute; top:0px; left:0px;}
	.tx{position: absolute; bottom: -18px; left: -18px; width: 100%; z-index: 1000; text-align: center; font-weight: 700;} 
</style>


