<template>
    <div class="ourservice">
        <navComponent/>
        <header-vue/>

        <div class="ride pt-5">
            <div class="container">
                <div class="headerTitle" style="font-size:24px; font-weight:700;">
                    Our Fleet
                </div>
                <div class="row mt-4">
                    <div class="col-md-3 mt-5" v-for="(i,index) in ride" :key="index">
                        <div class="rideBox">
                            <div class="rideBoxIcon">
                                <img :src="i.img" style="width:100px; height:75px;" alt="">
                            </div>
                            <div class="rideBoxText pt-3">
                                {{i.title}}
                            </div>
                            <p style="text-align:center;">
                                {{i.content}}
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>

         <div class="ride pt-5">
            <div class="container">
                <div class="headerTitle" style="font-size:24px; font-weight:700;">
                    Drive
                </div>
                <div class="row mt-1">
                    <div class="col-md-3 mt-5" v-for="(i,index) in drive" :key="index">
                        <div class="rideBox">
                            <a :href="i.at" class="noLinkStyle">
                                <div class="rideBoxText pt-1">
                                    {{i.title}}
                                </div>
                            </a>
                            
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="mt-5"></div>
        <footerContent/>
    </div>    
</template>
<script>
import navComponent from '@/Components/Global/navComponent.vue';
import headerVue from './ourservice/headerVue.vue';
import footerContent from '@/Components/Global/footerContent.vue';
export default {
    components:{navComponent,headerVue,footerContent},
    data() {
        return {
            ride:[
                    {icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R 4',more:false,img:require('@/assets/carSlider/r4.png'),content:'This vehicle is with 4 passenger seats'},
                    {icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R 6',more:false,img:require('@/assets/carSlider/r6.png'),content:' This vehicle is with 6 passenger seats'},
                    {icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R share',more:false,img:require('@/assets/carSlider/rshare.png'),content:'It is a common transport category that carries more than one passenger in the vehicle during the trip'},
                    {icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R suv',more:false,img:require('@/assets/carSlider/rsuv.png'),content:'This vehicle is high class SUV ( sport utility vehicle )'},
                    {icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R lux',more:false,img:require('@/assets/carSlider/rlux.png'),content:'This vehicle is luxury type'},
                    {icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R taxi',more:false,img:require('@/assets/carSlider/rtaxi.png'),content:'This vehicle is ( TAXI ) type'},
                    {icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R Bus',more:false,img:require('@/assets/carSlider/rbus.png'),content:'This vehicle is minibus type of 11-14 passenger seats and has a specific itinerary and stops'},
                ],
            drive:[
                {title:'Drive',at:'#/driverFuture'},
                {title:'Ride',at:'#/riderFuture'},
                {title:'Delivery'}
            ]
        }
    }
}
</script>
<style>
.rideBox{padding: 20px 0px; background-color: rgba(0,0,0,0.2); border:1px solid black; border-radius: 4px; text-align: center; cursor: pointer;}
.rideBoxIcon{font-size: 22px;font-weight: 700;}
.rideBoxText{font-weight: 700; font-size:18px;}

.rideBox a{text-decoration: none;}
</style>



